// import { densities, sizeScale } from '../config';
import { config } from '../config';

/**
 * Options for generating a selector with infix or postfix modifiers.
 * These infix or postfix values must be placed in different parts of
 * the generated selector depending upon if the selector root is a
 * `:host()` element or standard `#id`/`.class`.
 *
 * `nonHostInfix` and `nonHostPostfix` will override values passed
 * for `infix` and `postfix` if the the selector root is not `:host`.
 * This is so that the selector can be used to provide different
 * behavior when it is dynamically with both :host and id/class
 * selectors (most likely in mixins).
 */
export interface SelectorOptions {
  infix?: string;
  postfix?: string;
  nonHostInfix?: string;
  nonHostPostfix?: string;
}

/**
 * Special type that defines a SelectorFactory function.
 * These functions work exactly like global selector() function
 * except the selectorRoot and some possible infix or postfix values
 * have already been set by a parent mixin.
 */
export type SelectorFactory = (options?: SelectorOptions) => string;

/** Determines if a class is `:host` or not */
export const isHostClass = (selectorRoot: string) =>
  [
    ':host:host:host:host:host:host', // first since it will be the most common
    ':host',
    ':host:host',
    ':host:host:host',
    ':host:host:host:host',
    ':host:host:host:host:host',
    ':host:host:host:host:host:host:host',
    ':host:host:host:host:host:host:host:host',
  ].includes(selectorRoot);

/**
 * Core building block of our CSS in JS "mixins"
 *
 * This factory allows mixins to declare their own selector root
 * alongside their own infix and/or postfix options. The factory returns
 * a localized custom selector function that the user can call with
 * additional infix or postfix values so that the selector can be used
 * multiple times within the scope of the "mixin" callback.
 */
export const selectorFactory =
  (
    selectorRoot: string,
    {
      infix: internalInfix = '',
      postfix: internalPostfix = '',
      nonHostInfix: internalNonHostInfix,
      nonHostPostfix: internalNonHostPostfix,
    }: SelectorOptions = {}
  ): SelectorFactory =>
  ({
    infix = '',
    postfix = '',
    nonHostInfix = null,
    nonHostPostfix = null,
  }: SelectorOptions = {}) => {
    if (isHostClass(selectorRoot)) {
      if (internalInfix || infix) {
        return `${selectorRoot}(${internalInfix ?? ''}${infix ?? ''})${internalPostfix ?? ''}${
          postfix ?? ''
        }`;
      } else {
        return `${selectorRoot}${internalPostfix ?? ''}${postfix ?? ''}`;
      }
    } else {
      return `${selectorRoot}${internalNonHostInfix ?? internalInfix ?? ''}${
        nonHostInfix ?? infix ?? ''
      }${internalNonHostPostfix ?? internalPostfix ?? ''}${nonHostPostfix ?? postfix ?? ''}`;
    }
  };

/**
 * Function used for generating a selector with infix or postfix modifiers.
 * These infix or postfix values must be placed in different parts of
 * the generated selector depending upon if the selectorRoot is a
 * `:host()` element or standard `#id`/`.class`.
 *
 * `nonHostInfix` and `nonHostPostfix` will override values passed for `infix`
 * and `postfix` if the the selector root is not `:host`. This is so that the
 * selector can be used to provide different behavior when it is dynamically
 * with both :host and id/class selectors (most likely in mixins).
 */
export const selector = (
  selectorRoot: string,
  { infix = '', postfix = '', nonHostInfix = null, nonHostPostfix = null }: SelectorOptions = {}
) => {
  if (isHostClass(selectorRoot)) {
    return infix
      ? `${selectorRoot}(${infix ?? ''})${postfix ?? ''}`
      : `${selectorRoot}${postfix ?? ''}`;
  } else {
    return `${selectorRoot}${nonHostInfix ?? infix ?? ''}${nonHostPostfix ?? postfix ?? ''}`;
  }
};

export const sizeModifier = (value: number | string, size: string) => {
  return `calc(${value} * ${config.size.scale[size]});`;
};

export const stripUnit = (value: string) => {
  return value.replace(/{|px|pc|pt|in|mm|cm|ex|em|rem|vw|vh|vmin|vmax|ch|lh|}/, '');
};

export const stripUnitInt = (value: string) => {
  return parseInt(value.replace(/{|px|pc|pt|in|mm|cm|ex|em|rem|vw|vh|vmin|vmax|ch|lh|}/, ''), 10);
};

export const stripUnitFloat = (value: string) => {
  return parseFloat(value.replace(/{|px|pc|pt|in|mm|cm|ex|em|rem|vw|vh|vmin|vmax|ch|lh|}/, ''));
};

export const sizeVariantsMixin = (
  selectorRoot: string,
  content: (selector: SelectorFactory, variant: string) => string
) => {
  return Object.keys(config.size.scale).reduce((acc, variant) => {
    return `${acc} ${content(
      selectorFactory(selectorRoot, {
        infix: `[size="${variant}"]`,
        nonHostInfix: `.size--${variant}`,
      }),
      variant
    )}`;
  }, '');
};

export const densityVariantsMixin = (
  selectorRoot: string,
  content: (selector: SelectorFactory, variant: string) => string
) => {
  return config.size.densities.reduce((acc, variant) => {
    return `${acc} ${content(
      selectorFactory(selectorRoot, {
        infix: `[density="${variant}"]`,
        nonHostInfix: `.density--${variant}`,
      }),
      variant
    )}`;
  }, '');
};

export const disabled = () => {
  return `cursor: not-allowed !important`;
};

export const fillspacing = () => {
  return `
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: inherit;
  `;
};

export const visuallyHidden = () => {
  return `
    position: absolute !important;
    width: 1px !important;
    height: 1px !important;
    clip: rect(0 0 0 0) !important;
    clip-path: inset(50%) !important;
    border: none !important;
    overflow: hidden !important;
    white-space: nowrap !important;
    padding: 0 !important;
  `;
};
