// import { transitionDuration, timingFunction } from '../config';
import { config } from '../config';

export interface TransitionOptions {
  prop: string;
  duration?: string;
  delay?: string;
  timingFn?: string;
}

export const transition = ({
  prop,
  duration = config.animation.transitionDuration.default,
  delay = '0ms',
  timingFn = config.animation.timingFunction.standard,
}) => {
  return `${prop} ${duration} ${delay} ${timingFn}`;
};
