import { config } from '../config';

export const fontBaseMixin = (family: keyof typeof config.font.family = 'sans') => `
  font-family: ${config.font.family[family]};
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-decoration: inherit;
  text-transform: inherit;
`;

export interface fontSetOptions {
  s: keyof typeof config.font.set;
  ratio?: typeof config.font.ratio[keyof typeof config.font.ratio];
  fontFamily?: keyof typeof config.font.family;
  important?: boolean;
}

export const fontSetMixin = ({
  s: set,
  ratio,
  fontFamily = 'sans',
  important = false,
}: fontSetOptions) => {
  const typeStyle = config.font.set[set];
  if (ratio) {
    return `
      ${fontBaseMixin(fontFamily)}
      font-size: calc(${typeStyle.fontSize} * ${ratio})${important ? ' !important' : ''};
      font-weight: ${typeStyle.fontWeight}${important ? ' !important' : ''};
      line-height: ${typeStyle.lineHeight}${important ? ' !important' : ''};
    `;
  } else {
    return `
      ${fontBaseMixin(fontFamily)}
      font-size: ${typeStyle.fontSize}${important ? ' !important' : ''};
      font-weight: ${typeStyle.fontWeight}${important ? ' !important' : ''};
      line-height: ${typeStyle.lineHeight}${important ? ' !important' : ''};
    `;
  }
};

export const fontOverflowEllipsisMixin = () => `
  text-overflow: ellipsis;
  white-spacing: nowrap;
  overflow: hidden;
`;

export const fontSetInheritMixin = () => `
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  text-decoration: inherit;
  text-transform: inherit;
`;
