import { config } from '../config';
import { selector, selectorFactory, SelectorOptions } from './core';
import { CSSResult } from 'lit';

const dashCase = (s) => {
  return s
    .replace(/[A-Z]/g, (m) => '-' + m.toLowerCase())
    .replace(/(.*)([0-9]|[A-Z])(.*)/, '$1-$2$3');
};

export const paletteGroupOptions = {
  core: ['primary', 'secondary', 'tertiary', 'danger'],
  status: ['primary', 'secondary', 'tertiary', 'success', 'warning', 'danger'],
  viz: ['one', 'two', 'three', 'four', 'five', 'six'],
};

const getOpacity = (opacity: keyof typeof config.opacity | number | string) =>
  (opacity = config.opacity[`${opacity}`] ?? opacity);

export interface ColorOptions {
  g?: 'core' | 'status' | 'viz';
  p: string;
  c?: 'lighter' | 'light' | 'base' | 'dark' | 'darker';
  o?: keyof typeof config.opacity | number | string;
}

export const color = ({
  g: group = 'core',
  p: palette,
  c: color = 'base',
  o: opacity = null,
}: ColorOptions) => {
  opacity = getOpacity(opacity);
  if (opacity) {
    return `rgba(var(--mte-${dashCase(group)}-${dashCase(palette)}-${color}-rgb), ${opacity})`;
  } else {
    return `rgb(var(--mte-${dashCase(group)}-${dashCase(palette)}-${color}-rgb))`;
  }
};

export const colorContrast = ({
  g: group = 'core',
  p: palette,
  c: color = 'base',
  o: opacity = null,
}: ColorOptions) => {
  opacity = getOpacity(opacity);
  if (opacity) {
    return `rgba(var(--mte-${dashCase(group)}-${dashCase(palette)}-${color}-c-rgb), ${opacity})`;
  } else {
    return `rgb(var(--mte-${dashCase(group)}-${dashCase(palette)}-${color}-c-rgb))`;
  }
};

/**
 * Calls the callback with each palette in the requested palette group
 * then combines all of the returned CSS template strings.
 */
export const forEachPGVariant = <PaletteGroup extends keyof typeof paletteGroupOptions>(
  group: PaletteGroup,
  callback: (group: PaletteGroup, palette: string) => CSSResult | string
) => {
  return paletteGroupOptions[group].reduce(
    (acc, palette) => `${acc}${callback(group, palette)}`,
    ''
  );
};

export interface TokenOptions {
  n: keyof typeof config.color;
  o?: keyof typeof config.opacity | number | string;
}

export const token = ({ n: name, o: opacity = null }: TokenOptions) => {
  opacity = getOpacity(opacity);
  if (opacity) {
    return `rgba(var(--mte-${dashCase(name)}-rgb), ${opacity})`;
  } else {
    return `rgb(var(--mte-${dashCase(name)}-rgb))`;
  }
};

export interface StateTokenOptions {
  n: keyof typeof config.stateToken;
  s: string;
  o?: keyof typeof config.opacity | number | string;
}

export const stateToken = ({ n: name, s: state, o: opacity }: StateTokenOptions) => {
  opacity = getOpacity(opacity);
  if (opacity) {
    return `rgba(var(--mte-it-${dashCase(name)}-${state}-rgb), ${opacity})`;
  } else {
    return `rgba(var(--mte-it-${dashCase(name)}-${state}-rgb), var(--mte-it-${dashCase(
      name
    )}-${state}-a))`;
  }
};

export interface StateTokenMixinOptions {
  selectorOptions?: SelectorOptions;
  n: keyof typeof config.stateToken;
  forStates?: string[];
  forProps?: string[];
  autoContrast?: boolean;
  useImportant?: boolean;
  asOverlay?: boolean;
}

export const stateTokenMixin = (
  selectorRoot: string,
  {
    selectorOptions = { infix: '', postfix: '' },
    n: name,
    forStates = ['default', 'hover', 'active'],
    forProps = ['background'],
    useImportant = false,
    asOverlay = false,
  }: StateTokenMixinOptions
) => {
  if (asOverlay) {
    forStates = forStates.filter((state) => state !== 'default');
  }
  const selector = selectorFactory(selectorRoot, selectorOptions);
  const setInteractiveProps = ({ name, state, forProps, useImportant = false }) => {
    return forProps.reduce((acc, prop) => {
      return `${acc}
        ${prop}: ${stateToken({ n: name, s: state })}${useImportant ? ' !important;' : ';'}
      `;
    }, '');
  };

  return forStates.reduce((acc, state) => {
    return `${acc}
      ${selector({ infix: state !== 'default' ? `:${state}` : '' })}${
      asOverlay
        ? `::before {
          content: "";
          position: absolute;
          top: 0;
          right: 0;
          left: 0;
          bottom: 0;
          border-radius: inherit;`
        : ` {
      `
    }
        ${setInteractiveProps({ name, state, forProps, useImportant })}
      }
    `;
  }, '');
};

export interface FocusOptions {
  selectorOptions?: SelectorOptions;
  selectorState?: ':focus-visible' | ':focus' | ':hover' | null;
  selectorOutlet?: '::before' | '::after';
  flush?: boolean;
  radius?: string;
  inset?: string;
  radiusTopLeft?: string;
  radiusTopRight?: string;
  radiusBottomLeft?: string;
  radiusBottomRight?: string;
  offsetTop?: string;
  offsetRight?: string;
  offsetBottom?: string;
  offsetLeft?: string;
}

export const focusedMixin = (
  selectorRoot: string,
  {
    selectorOptions = { infix: '', postfix: '' },
    selectorState = ':focus-visible',
    selectorOutlet = '::before',
    flush = false,
    radius = '4px',
    inset,
    radiusTopLeft,
    radiusTopRight,
    radiusBottomLeft,
    radiusBottomRight,
    offsetTop = '-4px',
    offsetRight = '-4px',
    offsetBottom = '-4px',
    offsetLeft = '-4px',
  }: FocusOptions
) => {
  if (flush) {
    offsetTop = '0px';
    offsetRight = '0px';
    offsetBottom = '0px';
    offsetLeft = '0px';
  }
  if (!radiusTopLeft) {
    radiusTopLeft = radius;
  }
  if (!radiusTopRight) {
    radiusTopRight = radius;
  }
  if (!radiusBottomLeft) {
    radiusBottomLeft = radius;
  }
  if (!radiusBottomRight) {
    radiusBottomRight = radius;
  }

  return `${selector(selectorRoot, {
    ...selectorOptions,
    infix: `${selectorOptions.infix ?? ''}${
      selectorState ?? ''
    }:not(.disabled):not([disabled]):not(:disabled)`,
    postfix: `${selectorOptions.postfix ?? ''}${selectorOutlet ?? ''}`,
  })} {
    display: block;
    position: absolute;
    ${
      inset
        ? ''
        : `top: ${offsetTop};
      right: ${offsetRight};
      bottom: ${offsetBottom};
      left: ${offsetLeft};`
    }
    ${inset ? `inset: ${inset};` : ''}
    border: solid 2px ${token({ n: 'focus' })};
    box-shadow: 0 0 0 1px var(--mte-light);
    border-top-left-radius: ${radiusTopLeft};
    border-top-right-radius: ${radiusTopRight};
    border-bottom-left-radius: ${radiusBottomLeft};
    border-bottom-right-radius: ${radiusBottomRight};
    pointer-events: none;
    content: '';
    z-index: 9;
  }`;
};
